___CSS_LOADER_EXPORT___.push([module.id, `/* SignIn.css in styles folder */

/* Container styles */
.signin-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #f9f9f9;
  padding: 1rem;
}

/* Card styles */
.signin-card {
  background: white;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
  text-align: center;
}

/* Title styles */
.signin-title {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  color: #333;
}

/* Error message */
.signin-error {
  color: red;
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

/* Form styles */
.signin-form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 1rem;
  text-align: left;
}

label {
  font-size: 0.9rem;
  color: #555;
}

input {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
}

input:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 4px rgba(0, 123, 255, 0.3);
}

/* Button styles */
.signin-button {
  background: #007bff;
  color: white;
  border: none;
  padding: 0.75rem;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 1rem;
}

.signin-button:hover {
  background: #0056b3;
}

/* Links */
.signin-links {
  margin-top: 1rem;
  text-align: center;
}

.forgot-password-link,
.signup-link {
  color: #007bff;
  text-decoration: none;
}

.forgot-password-link:hover,
.signup-link:hover {
  text-decoration: underline;
}
